.container {
  display: flex;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 576px) {
  .container {
    max-width: 540px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media screen and (min-width: 990px) {
  .container {
    max-width: 960px;
  }
}

@media screen and (min-width: 1400px) {
  .container {
    max-width: 1366px;
  }
}
